@import url(https://fonts.googleapis.com/css?family=Questrial&display=swap);
@import url(https://fonts.googleapis.com/css?family=Courier+Prime&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script:700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Questrial", Helvetica, Arial, sans-serif;
  line-height: 1.4;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#277462), to(#3c2774));
  background-image: linear-gradient(#277462, #3c2774);
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
  padding: 3%;
}

a {
  color: #56c6ac;
  text-decoration: none;
}

a:hover {
  color: #3c2774 !important;
  text-decoration: none;
}

.container {
  padding: 0 1rem;
}

.flex-container {
  display: flex;
  align-items: center;
}

.addHabitInput {
  color: #3c2774 !important;
  padding: 5px 15px 5px 15px;
  background: #cdeee7; 
  border: 1px solid #cdeee7;
  border-radius: 15px;
  margin: 10px 10px 10px 0px;
  -webkit-transition-duration: 0.25s;
          transition-duration: 0.25s;
}

.addHabitInput:focus {
  outline: none !important;
  border: 1px solid #239379 !important;
}

.btn {
  display: inline-block;
  border: none;
  border-radius: 15px;
  background: #56c6ac;
  margin: 10px 0px 10px 0px;
  color: #ffffff;
  padding: 7px 20px;
  cursor: pointer;
  -webkit-transition-duration: 0.25s;
          transition-duration: 0.25s;
}

.btn:hover {
  background: #239379;
}

.buttonsBox {
  text-align:center !important;
}

.btnNum:hover {
  background-color: #239379 !important;
}

.titleDisplay {
  padding-left: 10px;
}

.numItemsDisplay {
  text-align: right;
  padding-right: 10px;
}

.btnDel:hover {
  background: #742739 !important;
  color: #ffffff;
  border: none;
  padding: 5px 9px;
  border-radius: 50%;
  cursor: pointer;
  float: right;
}
